<template>
  <div style="width: 95%;" v-loading="loading">
    <el-row ref="row" style="height: 88px;">
      <el-scrollbar v-if="files.length>0" style="height:100%;" :native="false">
        <el-col :span="3" v-for="file in files" :key="file.FILEID" style="margin:0px;height:auto">
          <el-tooltip :content="calcFileSize(file.FILESIZE)" placement="bottom">
          <div style="border: 0px;text-align:center; height: 100%" :title="file.DESCRIOTIO">
            <img :src="'../../static/assets/files/' + file.FILETYPE.toLowerCase() + '_48px.png'" style="width:48px; height: 48px;box-shadow:5px 2px 6px #000"/>
            <div style="padding: 0px">
              {{ file.DESCRIOTIO }}
            </div>
          </div>
          </el-tooltip>
        </el-col>
      </el-scrollbar>
      <el-col v-else :span="24">
        <h1>无数据</h1>
      </el-col>
    </el-row>
  </div>
</template>
<script>
export default {
  name: 'FilesView',
  props: ['docId'],
  created: function () {
    this.$nextTick(function () {
      this.getFiles()
    })
  },
  data () {
    return {
      loading: true,
      count: 0,
      emptyFile: {
        DESCRIOTIO: '无数据'
      },
      files: []
    }
  },
  methods: {
    calcFileSize: function (fileSize) {
      var MAX_KB = Math.pow(1024, 2)
      if (fileSize >= 0 && fileSize <= MAX_KB) {
        return (fileSize / 1024).toFixed(2) + ' KB'
      }
      var MAX_MB = Math.pow(1024, 3)
      if (fileSize >= MAX_KB && fileSize <= MAX_MB) {
        return (fileSize / MAX_KB).toFixed(2) + ' MB'
      }
      var MAX_GB = Math.pow(1024, 4)
      if (fileSize >= MAX_MB && fileSize <= MAX_GB) {
        return (fileSize / MAX_MB).toFixed(2) + ' GB'
      }
      return (fileSize / MAX_GB).toFixed(2) + ' TB'
    },
    getFiles: function () {
      let that = this
      var array = this.$parent.$el.parentElement.classList[0]
      if (array.indexOf('el-table__body-wrapper') !== -1) {
        this.axios.get(this.api + '/GetFiles.do', {
          params: {
            docId: this.docId
          }
        }).then(function (res) {
          that.loading = false
          if (res.data.code === 0) {
            that.files = res.data.list
          } else {
            that.files = []
          }
        }).catch(function () {
          that.loading = false
          that.files = []
        })
      }
    }
  }
}
</script>
<style>
.el-scrollbar__wrap {
  overflow-x: hidden;
}
</style>



