<template>
  <div style="height: 100%;">
    <avue-crud
      :data="data"
      :option="option"
      :page="page"
      :table-loading="loading"
      @size-change="sizeChange"
      @row-update="rowUpdate"
      @refresh-change="refreshChange"
      @current-change="currentChange">
      <template slot-scope="props" slot="expand">
        <files-view :doc-id="props.row.DOCID" @change="myChange"></files-view>
      </template>
    </avue-crud>
  </div>
</template>

<script>
import ArchiveHome from '@/components/ArchiveHome'
import EasyStarMenu from '@/components/docManager/nav/EasyStarMenu'
import FilesView from '@/components/fileManager/FilesView'
export default {
  name: 'DocMain',
  components: {
    EasyStarMenu: EasyStarMenu,
    FilesView: FilesView,
    ArchiveHome: ArchiveHome
  },
  data () {
    return {
      title: '档案管理',
      classId: '',
      classType: '',
      data: [],
      loading: false,
      page: {
        pageSizes: [20, 40, 80, 100],
        pageSize: 10,
        currentPage: 1,
        total: 0
      },
      option: {
        expand: true,
        highlightCurrentRow: true,
        height: '510px',
        addBtn: true,
        border: true,
        stripe: true,
        showHeader: true,
        index: false,
        size: 'mini',
        selection: false,
        page: true,
        align: 'center',
        menu: true,
        menuAlign: 'center',
        menuType: 'menu',
        menuWidth: '120px',
        column: []
      }
    }
  },
  mounted: function () {
    this.$nextTick(function () {
      this.page.pageSize = this.page.pageSizes[0]
      this.fetchData()
    })
  },
  watch: {
    // 如果路由有变化，会再次执行该方法
    '$route': 'fetchData'
  },
  methods: {
    myChange: function () {
      console.log(this)
    },
    currentChange: function (obj) {
      this.page.currentPage = obj
      this.fetchData()
    },
    refreshChange: function (obj) {
      this.fetchData()
    },
    rowUpdate: function (row, index, done, loading) {
      console.log('3=', row, index, done, loading)
    },
    sizeChange: function (pageSize) {
      this.page.pageSize = pageSize
      this.fetchData()
    },
    parseCols: function (cols) {
      var newCols = []
      cols.forEach(element => {
        var newCol = {}
        newCol.label = element.label
        newCol.prop = element.prop
        newCol.overHidden = true
        newCols.push(newCol)
      })
      this.option.column = newCols
    },
    fetchData: function () {
      console.log(this.page.pageSize, this.page.currentPage)
      let that = this
      this.classId = this.$route.params.classId
      this.classType = this.$route.params.classType
      this.option.expand = false
      if (typeof (this.classId) !== 'undefined' && typeof (this.classType) !== 'undefined' && this.classId !== '' && this.classType !== '') {
        var url = ''
        switch (this.classType) {
          case '0':
            url = '/DocTable.do'
            this.option.expand = true
            break
          case '1':
            url = '/BoxTable.do'
            break
          case '2':
            url = '/RoolTable.do'
            break
        }
        this.loading = true
        this.axios.get(this.api + url, {
          params: {
            classId: this.classId,
            pageSize: this.page.pageSize,
            pageIndex: this.page.currentPage
          }
        }).then(
          function (res) {
            console.log(res)
            that.loading = false
            that.parseCols(res.data.cols)
            that.data = res.data.list
            that.page.total = res.data.count
          }
        ).catch(
          function () {
            that.loading = false
          }
        )
      }
    }
  }
}
</script>

<style>
.el-table--striped .el-table__body tr.el-table__row--striped.current-row td, .el-table__body tr.current-row > td {
  background: rgb(68, 208, 233);
  font-weight: 700;
  color: #000;
}
</style>



